import React from 'react';
import './App.css';
import Head from './component/Head'
import Mid from './component/Mid'
import Project from './component/Projects';
import Lower from './component/Lower';
import Gitcal from './gitcal';
import Check from './component/check';
import Experience from './component/Experience';
import {useState} from 'react'
import GitHubCalendar from 'react-github-calendar';
import ReactTooltip from 'react-tooltip'

export default class App extends React.Component {
  constructor(props){
    super(props)
    this.state={
      modecolor:'black',
      darkmode:false,
      iconcolor:'white',
      projecthead:'black'
    }
  }
  dark = () =>{             // Enables the dark mode
   
    document.body.style.backgroundColor = "black";
    document.body.style.color = "white";

    this.setState({
      modecolor:'white',
      darkmode:true,
      projecthead:'black'
    })
  }

  white = () =>{              //Enables the normal white mode
    document.body.style.backgroundColor = "white";
    document.body.style.color = "black";
    this.setState({
      modecolor:"black",
      darkmode:false,
      projecthead:'black'
    
    })
  }

  render() {
    
    return (
      <div>
        <div className="App" id="main">
      <Check />
      <Head darkmode={this.state.darkmode} sendwhite={this.white} sendark = {this.dark}  iconclr={this.state.iconcolor} nameclr={this.state.modecolor}/>
      <Mid />
      <div className="mt-5">
        <Project  iconclr={this.state.projecthead} />
        <div>
          <div className="mt-5">
            <Experience />
          </div>
          <Lower />
              <GitHubCalendar style={{marginTop:"20px"}} color="#3b5998" username="harshit860">
                <ReactTooltip delayShow={50} html />
              </GitHubCalendar>
       
        </div>
      </div>

    </div>
  
      </div>
    )
  }
}
